<template>
  <div class="secondpage-container">
    <div class="flip-card" :class="{ flipped }">
      <div class="flip-card-inner">
        <div class="flip-card-front" :style="{ 'background-image': `url(${resources.photos[0]})` }">
        </div>
        <div class="flip-card-back">
          <div ref="textContainer" class="text-container no-scrollbar">
            <p v-for="(section, i) in sections" :key="`section-${i}`" class="text text-shadow">{{ section.text }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { onMounted, reactive, ref, watch } from 'vue'
import { useSentencesTyping } from '@/utils/text-typing'
import { useResources } from '@/utils/load-resources'
import { delay } from '@/utils'

export default {
  setup (props, { emit }) {
    const resources = useResources()

    const textContainer = ref(null)

    const paragraph = 'ÈÇÑË®òÂæóÂâõË™çË≠òÁöÑÊôÇÂÄô‰Ω†Á∏ΩÊòØË™™ËëóËá™Â∑±ÂèØËÉΩÊúÉËÆìÊàëÂ§±Êúõ„ÄÅÁôºÁèæ‰Ω†‰∏çÂ§†Â•ΩÂïä‰ªÄÈ∫ºÁöÑÈÄôÁ®ÆÊ≤íÊúâËá™‰ø°ÁöÑË©± ü•∫\nÁèæÂú®ÈõñÁÑ∂Â∞±ÊàëÁúã‰æÜÈõ¢ÂÖÖÊªøËá™‰ø°ÈÇÑÊúâ‰∏ÄÊÆµË∑ØÁ®ãË¶ÅËµ∞ ü§î\n‰ΩÜÊòØÁõ∏‰ø°‰Ω†ÂõûÈ†≠ÁúãÁúãÈÅéÂéªËàáÁèæÂú®ÁöÑËá™Â∑±Ôºå\n‰Ω†ÊúÉË∑üÊàë‰∏ÄÊ®£ÂæàÈñãÂøÉÁôºÁèæÂà∞‰Ω†Â∑≤Á∂ìÈÄ≤Ê≠•‰∫ÜË®±Â§ö üòÜ\nËÅΩÂà∞ÊàëË™™Ë∑ü‰Ω†ËÅäÂ§©ÂæàÈñãÂøÉÁöÑ‰Ω†ÔºåÊàëÁúüÁöÑË¶∫ÂæóÂ•ΩÂèØÊÑõÔºå\nË∑ü‰Ω†ËÅäÂ§©Á∏ΩÊòØËÉΩÈùûÂ∏∏Ëá™ÁÑ∂‰∏çÈ°ßÊÖÆÂ§™Â§öÔºå\n‰Ω†ÁöÑÊííÂ¨å‰πüÈÉΩËÆìÊàëË¶∫ÂæóÂ•ΩÊÉ≥ÊääÂ¶≥Êä±Á∑äÂ•ΩÂ•ΩÁñºÊÑõ ‚ù§Ô∏è'

    const sections = reactive(paragraph.split('\n')
      .map((text) => ({
        text,
        keep: true
      }))
      .map((sentence) => {
        const { text, typeSentences } = useSentencesTyping()

        return {
          text,
          startTyping: () => typeSentences([sentence])
        }
      }))

    const flipped = ref(false)

    onMounted(async () => {
      document.querySelector('.secondpage-container').classList.add(...['animate__animated', 'animate__fadeIn'])
      await delay(2000)
      document.querySelector('.secondpage-container').classList.remove(...['animate__animated', 'animate__fadeIn'])
      flipped.value = true
      await delay(1000)
      for (const section of sections) {
        await section.startTyping()
      }
      await delay(2000)
      document.querySelector('.secondpage-container').classList.add(...['animate__animated', 'animate__fadeOut'])
      await delay(1000)
      emit('next')
    })

    watch(() => sections.map(({ text }) => text), () => {
      const el = textContainer.value
      el.scrollTo({
        top: el.scrollHeight,
        behavior: 'smooth'
      })
    })

    return {
      resources,
      textContainer,
      flipped,
      sections
    }
  }
}
</script>

<style lang="scss">
.secondpage-container {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;

  .flip-card {
    background-color: transparent;
    width: min(90%, 30rem);
    height: 80vh;
    perspective: 60rem;

    .flip-card-inner {
      position: relative;
      width: 100%;
      height: 100%;
      transition: transform 0.6s;
      transform-style: preserve-3d;
      box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
      border-radius: 1.25rem;
    }

    &.flipped .flip-card-inner {
      transform: rotateY(-180deg);
    }

    .flip-card-front, .flip-card-back {
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: 1.25rem;
      backface-visibility: hidden;
      overflow: hidden;
    }

    .flip-card-front {
      color: black;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: 50% 80%;
    }

    .flip-card-back {
      padding: 1.5rem;
      background-color: #ffe7e7;
      color: white;
      transform: rotateY(180deg);

      .text-container {
        max-height: 100%;
        overflow: auto;

        .text {
          margin-bottom: 1rem;
          font-size: 1.5rem;
          line-height: 1.5;
        }
      }

    }
  }
}
</style>
